import Navbar from "./components/Navbar";
import TextForm from "./components/TextForm";
// import About from "./components/About";
import React, {useState} from 'react'
import Alert from "./components/Alert";
// import ReactDOM from "react-dom/client";
// import {
//   BrowserRouter,
//   Routes,
//   Route,
// } from "react-router-dom";

function App() {
  const [mode, setMode] = useState('light')
  const [alert, setAlert] = useState('')

  const showAlert = (message, type) =>{
        setAlert({
          msg: message,
          type: type 
        })
        setTimeout(() => {
          setAlert('')
        }, 1500);
  }

  const toggleMode =()=>{
    if(mode === 'light'){
      setMode('dark')
      document.body.style.backgroundColor = 'grey'
      showAlert("Dark mode has been enabled", "success");
    }
    else{
      setMode('light')
      document.body.style.backgroundColor = 'white'
      showAlert("Light mode has been enabled", "success");
    }
  }
  
  return (
    <>
       
    {/* <BrowserRouter> */}
      
       <Navbar title="TextUt" aboutText="About Us" mode={mode} toggleMode={toggleMode} />
       <Alert alert={alert} />
       <div className="container my-3">
          {/* <Routes> */}

          {/* <Route exact path="/about" element={<About />} /> */}
          {/* <Route exact path="/" element={<TextForm  heading ="Enter Text here to analyze" mode={mode} showAlert={showAlert} />} /> */}
      
          {/* </Routes> */}
          <TextForm  heading ="Try TextUtils - Word Counter, Character Counter and Capitalize Text" mode={mode} showAlert={showAlert} />
       </div>
    
      
    {/* </BrowserRouter> */}
        
      
       
        {/* <About /> */}
      </>
    );
}

export default App;
